<template>
  <div class="topic-page relative min-w-[1200px]">
    <page-header></page-header>
    <main class="w-[1200px] mx-auto">
      <div class="mt-5 pl-2 text-[#aeb9c0]">首页>在线工具><span class="blue-text">智能选题</span></div>
      <section>
        <div class="text-center mt-6">
          <img src="@/assets/topic_tool.png" class="w-[120px]" />
        </div>
        <h1 class="text-center text-[44px] mt-4 mb-12">回顾性队列研究选题工具</h1>
      </section>
      <section class="shadow-[0_0_14px_4px_rgba(0,0,0,0.1)] pl-4 pr-8 pt-4 pb-10 mb-12 rounded-lg">
        <el-form label-position="top" label-width="80px" :model="form" :rules="rules" ref="topicForm" class="flex flex-wrap">
          <el-form-item label="大疾病背景" prop="big_disease_background" class="w-2/4 pl-4">
            <el-input v-model="form.big_disease_background" placeholder="例如：心内科、心外科" maxlength="100"></el-input>
          </el-form-item>
          <el-form-item label="小疾病背景" prop="small_disease_background" class="w-2/4 pl-4">
            <el-input v-model="form.small_disease_background" placeholder="例如：冠心病" maxlength="100"></el-input>
          </el-form-item>
          <el-form-item label="具体研究疾病" prop="specific_disease" class="w-2/4 pl-4">
            <el-input v-model="form.specific_disease" placeholder="例如：心律失常" maxlength="100"></el-input>
          </el-form-item>
          <el-form-item label="目标治疗方法（暴露组）" prop="target_treatment_method" class="w-2/4 pl-4">
            <el-input v-model="form.target_treatment_method" placeholder="例如：射频消融术" maxlength="100"></el-input>
          </el-form-item>
          <el-form-item label="创新点" prop="innovation" class="w-2/4 pl-4">
            <el-input v-model="form.innovation" placeholder="请输入创新点内容" maxlength="100"></el-input>
          </el-form-item>
          <el-form-item label="对照组治疗方法（非暴露组）" prop="control_treatment_method" class="w-2/4 pl-4">
            <el-input v-model="form.control_treatment_method" placeholder="例如：心外膜消融术" maxlength="100"></el-input>
          </el-form-item>
          <el-form-item label="上传参考文献" class="w-full pl-4" prop="file">
            <file-upload ref="normalIntroductionRef" desc="为保证学习质量，暂不支持图片图表、音视频等最多可传20个;支持扩展名：.pdf" @getFile="getFile"></file-upload>
          </el-form-item>
        </el-form>
        <div class="text-center mt-8 mb-10">
          <el-button type="primary" size="medium" class="w-[200px]" @click="onSumbit">提交</el-button>
        </div>
      </section>
    </main>
    <section class="w-[1200px] mx-auto my-12 px-9 border border-dashed border-[#2b323d] flex items-center justify-between h-[150px]" v-if="file.name">
      <div class="flex items-center">
        <img class="w-[64px]" src="@/assets/word.png" />
        <div class="flex-1 overflow-hidden ml-3">
          <div class="break-all truncate">{{ file.name }}</div>
          <div class="mt-2 text-xs text-[#999]">{{ bytesToSize(file.size) }}</div>
        </div>
      </div>
      <div>
        <el-button class="w-[150px]" @click="onFileView">预览</el-button>
        <el-button type="primary" class="w-[150px]" @click="onDownload">下载文件</el-button>
      </div>
    </section>
    <page-footer></page-footer>
  </div>
</template>

<script>
import PageHeader from "@/views/components/PageHeader.vue"
import PageFooter from "@/views/components/PageFooter.vue"
import FileUpload from "@/views/components/FileUpload.vue"
import { bytesToSize } from "@/utils/validate"

export default {
  components: {
    PageHeader,
    PageFooter,
    FileUpload,
  },
  data() {
    return {
      form: {
        big_disease_background: "",
        small_disease_background: "",
        specific_disease: "",
        target_treatment_method: "",
        innovation: "",
        control_treatment_method: "",
        file: "",
      },
      rules: {
        big_disease_background: [{ required: true, message: "请输入大疾病背景", trigger: "blur" }],
        small_disease_background: [{ required: true, message: "请输入小疾病背景", trigger: "blur" }],
        specific_disease: [{ required: true, message: "请输入具体研究疾病", trigger: "blur" }],
        target_treatment_method: [{ required: true, message: "请输入目标治疗方法（暴露组）", trigger: "blur" }],
        innovation: [{ required: true, message: "请输入机构", trigger: "blur" }],

        control_treatment_method: [{ required: true, message: "请输入对照治疗方法（非暴露组）", trigger: "blur" }],
        file: [{ required: true, message: "请上传参考文献", trigger: ["blur", "change"] }],
      },
      file: {
        name: "",
        url: "",
        size: "",
      },
    }
  },
  methods: {
    bytesToSize(bytes) {
      return bytesToSize(bytes)
    },
    getFile(value) {
      this.form.file = value
    },
    onSumbit() {
      this.$refs["topicForm"].validate((valid) => {
        if (valid) {
          console.log("supplement submit!")
          resolve(this.form)
        } else {
          console.log("error supplement sumbit!")
          reject()
        }
      })
      this.file = {
        name: "心外膜消融术临床实际意义是否大于射频消融术",
        url: "/static/内镜下冷圈套切除术治疗微小型结直肠息肉疗效评估.pdf",
        size: "2344",
      }
    },
    onFileView(){
      window.open(this.file.url)
    },
    getFileExtension(filename) {
      return /[.]/.exec(filename) ? /[^.]+$/.exec(filename)[0] : ""
    },
    onDownload() {
      const url = this.file.url
      const fileExtension = this.getFileExtension(url)
      const name = this.file.name + "." + fileExtension
      this.downloadFileFromApi(url, name)
    },
    async downloadFileFromApi(fileUrl, fileName) {
      try {
        const response = await fetch(fileUrl)
        const blob = await response.blob()
        const url = URL.createObjectURL(blob)

        const link = document.createElement("a")
        link.href = url
        link.setAttribute("download", fileName)
        document.body.appendChild(link)
        link.click()

        // 清理工作
        document.body.removeChild(link)
        URL.revokeObjectURL(url)
      } catch (error) {
        console.error("下载失败:", error)
      }
    },
  },
  destroyed() {},
}
</script>
<style scoped lang="scss">
.topic-page {
  background: linear-gradient(to bottom, #ebfafb, #feffff), url("~@/assets/bg.png") no-repeat center top;
}
</style>
<style lang="scss">
.topic-page {
  .el-form--label-top .el-form-item__label {
    padding-bottom: 0;
  }

  .el-upload-dragger {
    padding-bottom: 20px;
  }

  .el-upload,
  .el-upload-dragger {
    width: 100%;
    height: auto;
  }

  .el-card {
    overflow: visible;
  }
}
</style>
